// models/orderModel.js
import mongoose from "mongoose";

const { Schema } = mongoose;

const orderItemSchema = new Schema({
  productId: { type: Schema.Types.ObjectId, ref: "Product", required: true },
  name: { type: String, required: true },           // snapshot: product name at time of order
  sizeLabel: { type: String, required: true },
  qty: { type: Number, required: true, min: 1 },
  unitPrice: { type: Number, required: true },      // snapshot price
  subtotal: { type: Number, required: true },       // unitPrice * qty (store to avoid recompute)
}, { _id: false });

const orderSchema = new Schema({
  customer: {
    name: { type: String, required: true },
    email: { type: String },
    phone: { type: String },
    address: { type: String },
  },
  items: { type: [orderItemSchema], required: true },
  // monetary totals (store in smallest currency unit or as integer rupees/paisa)
  subtotal: { type: Number, required: true },
  shipping: { type: Number, default: 0 },
  tax: { type: Number, default: 0 },
  discount: { type: Number, default: 0 },
  total: { type: Number, required: true },

  status: { 
    type: String, 
    enum: ["created","pending","paid","processing","shipped","delivered","cancelled","refunded"], 
    default: "created" 
  },

  payment: {
    provider: { type: String },     // e.g., "stripe","razorpay"
    paymentId: { type: String },
    method: { type: String },       // "card","upi","cod"
    paidAt: { type: Date }
  },

  fulfillment: {
    trackingNumber: String,
    carrier: String,
    shippedAt: Date,
    deliveredAt: Date
  },

  userId: { type: Schema.Types.ObjectId, ref: "User" }, // optional link to a user
  meta: { type: Schema.Types.Mixed }, // free-form metadata (webhooks, raw payment response)

  archived: { type: Boolean, default: false } // soft-archive
}, { timestamps: true });

orderSchema.index({ userId: 1, createdAt: -1 });
orderSchema.index({ "customer.email": 1 });
orderSchema.index({ status: 1, createdAt: -1 });

export default mongoose.models.Order || mongoose.model("Order", orderSchema);
