// src/pages/api/orders/index.js
import dbConnect from "lib/dbConnect";
import OrderService from "services/orderService";

/**
 * POST /api/orders -> create order
 * GET  /api/orders -> list orders (optional query params: page, limit)
 */

export default async function handler(req, res) {
  try {
    await dbConnect();
  } catch (err) {
    console.error("DB connect failed:", err);
    return res.status(500).json({ error: "Database connection failed" });
  }

  if (req.method === "POST") {
    try {
      const { customer, items } = req.body;

      // Basic validation
      if (!Array.isArray(items) || items.length === 0) {
        return res.status(400).json({ error: "items array is required" });
      }
      if (!customer || !customer.name) {
        return res.status(400).json({ error: "customer.name is required" });
      }

      const created = await OrderService.createOrder(customer, items);
      return res.status(201).json(created);
    } catch (err) {
      console.error("POST /api/orders error:", err);
      if (err.name === "InsufficientStockError") {
        return res.status(409).json({ error: err.message });
      }
      if (err.name === "OrderError") {
        return res.status(400).json({ error: err.message });
      }
      return res.status(500).json({ error: err.message || "Failed to create order" });
    }
  }

  if (req.method === "GET") {
    try {
      const { page = "1", limit = "20" } = req.query;
      const result = await OrderService.listOrders({ page: Number(page), limit: Number(limit) });
      return res.status(200).json(result);
    } catch (err) {
      console.error("GET /api/orders error:", err);
      return res.status(500).json({ error: "Failed to list orders" });
    }
  }

  res.setHeader("Allow", ["GET", "POST"]);
  return res.status(405).end(`Method ${req.method} Not Allowed`);
}
